export default {
	props: {
		// 显示与否
		show: {
			type: Boolean,
			default: true
		},
		// 通过v-model绑定current值
		value: {
			type: [String, Number],
			default: 0
		},
		// 整个tabbar的背景颜色
		bgColor: {
			type: String,
			default: '#ffffff'
		},
		// tabbar的高度，默认50px，单位任意，如果为数值，则为rpx单位
		height: {
			type: [String, Number],
			default: '50px'
		},
		// 非凸起图标的大小，单位任意，数值默认rpx
		iconSize: {
			type: [String, Number],
			default: 40
		},
		// 凸起的图标的大小，单位任意，数值默认rpx
		midButtonSize: {
			type: [String, Number],
			default: 90
		},
		// 激活时的演示，包括字体图标，提示文字等的演示
		activeColor: {
			type: String,
			default: '#303133'
		},
		// 未激活时的颜色
		inactiveColor: {
			type: String,
			default: '#606266'
		},
		// 是否显示中部的凸起按钮
		midButton: {
			type: Boolean,
			default: false
		},
		// 配置参数
		list: {
			type: Array,
			default () {
				return []
			}
		},
		// 切换前的回调
		beforeSwitch: {
			type: Function,
			default: null
		},
		// 是否显示顶部的横线
		borderTop: {
			type: Boolean,
			default: true
		},
	},
	data() {
		return {

		}
	},
	methods: {
		async clickHandler(index) {
			if(this.beforeSwitch && typeof(this.beforeSwitch) === 'function') {
				// 执行回调，同时传入索引当作参数
				let beforeSwitch = this.beforeSwitch(index);
				// 判断是否返回了promise
				if (!!beforeSwitch && typeof beforeSwitch.then === 'function') {
					await beforeSwitch.then(res => {
						// promise返回成功，
						this.switchTab(index);
					}).catch(err => {
						
					})
				} else if(beforeSwitch === true) {
					// 如果返回true
					this.switchTab(index);
				}
			} else {
				this.switchTab(index);
			}
		},
		// 切换tab
		switchTab(index) {
			// 发出事件和修改v-model绑定的值
			this.$emit('change', index);
			this.$emit('input', index);
		},
		// 计算角标的right值
		getOffsetRight(count, isDot) {
			// 点类型，count大于9(两位数)，分别设置不同的right值，避免位置太挤
			if(isDot) {
				return -20;
			} else if(count > 9) {
				return -40;
			} else {
				return -30;
			}
		}
	}
}
